@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.col__one{

}
.col__two{
    display: flex;
    flex-direction: column;
}
.col {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.container {
  position: relative;
  min-height: 100vh;
  overflow: hidden;
}

.container::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 100vh;
  width: 300vw;
  transform: translate(35%, 0);
  background-image: linear-gradient(-45deg, var(--primary-color) 0%, var(--secondary-color) 100%);
  transition: 1s ease-in-out;
  z-index: 6;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-bottom-right-radius: max(50vw, 50vh);
  border-top-left-radius: max(50vw, 50vh);
}

.container.sign-in::before {
  transform: translate(0, 0);
  right: 45%;
}

.container.sign-up::before {
  transform: translate(98%, 0);
  right: 50%;
}
.row {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100vh;
  }
.content-row {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 6;
  width: 100%;

}
.form-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width:100%;
  max-width: 28rem;
}


.form__button{
    cursor: pointer;
    width: 100%;
    padding: .6rem 0;
    border-radius: .5rem;
    border:none;
    background-color: var(--priramy-color);
    color: var(--secondary-color);
    font-size: 1.2rem;
    outline: none;
}
.form {
  padding: 1rem;
  background-color: var(--white);
  border-radius: 1.5rem;
  width: 100%;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  transform: scale(0);
  transition: .5s ease-in-out;
  transition-delay: 1s ;
}

.form p {
  margin: 1rem 0;
  font-size: .7rem;
}

.form span {
  margin-right: .4rem;
}

.input-group {
  position: relative;
  width: 100%;
  margin: 1rem 0;
}
.input-group i {
  position: absolute;
  top: 50%;
  left: 1rem;
  transform: translateY(-50%);
  font-size: 1.4rem;
}
.input-group input {
  width: 100%;
  padding: 1rem 3rem;
  font-size: 1rem;
  background-color: var(--gray);
  border-radius: 0.5rem;
  border: 0.125rem solid var(--white);
  outline: none ;
}
.input-group input:focus {
  border: 0.125rem solid var(--priramy-color);
}

.social-list {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 2rem 0;
  padding: 1rem;
  border-radius: 1.5rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  width: 100%;
  max-width: 28rem;
  transform: scale(0);
  transition: .5s ease-in-out;
  transition-delay: 1.2s;
}
.social-list>div {
    color: var(--white);
    margin: 0 .5rem;
    padding: .7rem;
    cursor: pointer;
    border-radius: .5rem;
    cursor: pointer;
    transform: scale(0);
    transition: .5s ease-in-out;
}
.social-list>div:nth-child(1) {
  transition-delay: 1.4s;
}

.social-list>div:nth-child(2) {
  transition-delay: 1.6s;
}

.social-list>div:nth-child(3) {
  transition-delay: 1.8s;
}


.icon__fb{
    background-color: var(--facebook-color);
}
.icon__yt{
    background-color: var(--youtube-color);
}
.icon__in{
    background-color: var(--insta-color);
}
.icon {
  color: var(--white);
  margin: 0 0.5rem;
  padding: 0.7rem;
  cursor: pointer;
  border-radius: 0.5rem;
  cursor: pointer;
}
.icon i {
  font-size: 1.5rem;
  transition: 0.4s ease-in-out;
}

.icon:hover i {
  transform: scale(1.5);
}


.text {
    margin: 4rem;
    color: var(--white);
}

.text h2 {
    font-size: 3.5rem;
    font-weight: 800;
    margin: 2rem 0;
    transition: 1s ease-in-out;
}

.text p {
    font-weight: 600;
    transition: 1s ease-in-out;
    transition-delay: .2s;
}

.text.sign-in h2, .text.sign-in p, .img.sign-in img {
    transform: translateX(-250%);
}

.text.sign-up h2, .text.sign-up p, .img.sign-up img {
    transform: translateX(250%);
}
.img img {
  width: 40vh;
  transition: 1s ease-in-out;
  transition-delay: .4s;
  border-radius: 50%;
  margin-top: 1rem;
}
:root {
  --primary-color: #4669c8;
  --secondary-color: #23335f;
  --black: #000000;
  --white: #ffffff;
  --gray: #efefef;
  --facebook-color: #4267B2;
  --youtube-color: #DB4437;
  --insta-color: #E1306C;
}
* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.body {
  height: 100vh;
  overflow: hidden;
}

.container.sign-in .form.sign-in, .container.sign-in .social-list.sign-in, .container.sign-in .social-list.sign-in>div, .container.sign-up .form.sign-up, .container.sign-up .social-list.sign-up, .container.sign-up .social-list.sign-up>div {
  transform: scale(1);
}

.pointer {
  cursor: pointer;
}

.container.sign-in .text.sign-in h2, .container.sign-in .text.sign-in p, .container.sign-in .img.sign-in img, .container.sign-up .text.sign-up h2, .container.sign-up .text.sign-up p, .container.sign-up .img.sign-up img {
  transform: translateX(0);
}


